  box-shadow: 0 4px 30px rgba(41, 54, 66, 0.1);
  border-radius: 5px;
  padding: 50px 49px 49px 50px;
}
.write-us__head {
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 55px;
  line-height: 128%;
  color: #141517;
  font-size: 25px;
  text-align: center;
}
.write-us__text {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 150%;
  color: #141517;
  margin-top: 15px;
  text-align: center;
}
.write-us__input {
  width: 270px;
  height: 50px;
  background-color: #ffffff;
  border: 1px solid #d7d9df;
  margin-top: 50px;
  padding-left: 26px;
  margin-top: 24px;
  border: 1px solid rgba(44, 46, 63, 0.3);
}
.write-us__input::placeholder {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 17px;
  color: #48494d;
  font-size: 14px;
}
.write-us__input:invalid {
  border: 1px solid #FE7865;
}
.write-us__invalid {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  line-height: 15px;
  color: #FE7865;
  font-size: 12px;
  position: absolute;
  display: none;
  width: 269px;
  top: 149px;
  right: 61px;
  top: 129px;
  right: 49px;
}
.write-us__input:invalid + .write-us__invalid {
  display: inline;
}
.write-us__input--tel {
  background-image: url("../img/asterisk.svg");
  background-repeat: no-repeat;
  background-position: 15px 6.25px;
  margin-right: auto;
}
.write-us__input--tel:invalid + .write-us__invalid {
  left: 50px;
  right: auto;
}
.write-us__input--email {
  padding-left: 15px;
}
.write-us__submit {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 250px;
  height: 75px;
  background-color: #FE7865;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ffffff;
  margin-top: 50px;
  width: 176px;
  height: 50px;
  border: none;
  margin-top: 20px;
  margin-top: 25px;
}
.write-us__submit:hover,
.write-us__submit:focus {
  background-color: #F17D4B;
}
@media (max-width: 1023px) {
  .write-us__wrapper {
    justify-content: center;
    margin: 0 auto;
    margin-top: 125px;
  }
  .write-us__form {
    position: relative;
    height: 311px;
  }
  .write-us__submit {
    width: 250px;
    margin-top: 20px;
  }
}
@media (max-width: 767px) {
  .write-us__wrapper {
    background-image: none;
    margin-top: 50px;
    height: auto;
  }
  .write-us__form {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    align-content: stretch;
    padding: 50px 16px;
    width: 100%;
    height: 441px;
    background-color: #fbf8f8;
    box-shadow: none;
    border-radius: 0;
  }
  .write-us__head,
  .write-us__text,
  .write-us__submit,
  .write-us__input {
    width: 288px;
    margin-top: 0;
  }
  .write-us__input--tel {
    margin-right: 0;
  }
  .write-us__invalid {
    width: 288px;
    top: 185px;
    right: auto;
  }
  .write-us__input--tel:invalid + .write-us__invalid {
    left: auto;
  }
  .write-us__input--email:invalid + .write-us__invalid {
    top: 255px;
    left: auto;
  }
}
.show-overlay .overlay {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
